import React from "react";
import { Card, CardContent, LinearProgress, Chip } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import DataTable from "react-data-table-component";
import Var from "../variable";
import "../styles/main.css";

export default function Npm({ darkState }) {
  const useStyles = makeStyles((theme) => ({
    root: {
      width: "100%",
      "& > * + *": {
        marginTop: theme.spacing(2)
      }
    },
    title: {
      color: darkState ? "#ffffff" : "#343a40",
      textShadow: `3px 3px 2px ${darkState ? "rgba(0, 0, 0, 1)" : "rgba(150, 150, 150, 1)"}`
    }
  }));
  const {
    npmPage: { title, packages }
  } = Var;
  const classes = useStyles();
  const theme = darkState ? "dark" : "default";
  const columns = [
    {
      name: "Name",
      selector: "name",
      sortable: true,
      wrap: true,
      cell: ({name}) => <a href={`https://www.npmjs.com/package/${name}`}>{name}</a>
    },
    {
      name: "Version",
      selector: "version",
      sortable: true,
      maxWidth: "100px"
    },
    {
      name: "Last Updated",
      selector: "published",
      sortable: true,
      maxWidth: "100px",
      cell: ({ published }) => new Date(published).toLocaleString().replace(",","")
    },
    {
      name: "Description",
      selector: "description",
      sortable: true,
      grow: 3,
      wrap: true
    }
  ];

  const [state, setState] = React.useState({
    pending: true
  });
  const { pending } = state;

  React.useEffect(() => {
    setState((prev) => ({
      ...prev,
      pending: false
    }));
  }, []);

  return (
    <div className="">
      <div className={[classes.title, "text-5xl font-bold mb-8"].join(" ")}>
        {title} ({packages.length})
      </div>
      <Card className="w-full mx-auto m-16 md:m-0">
        <CardContent className="flex flex-col items-center justify-center p-0 py-20">
          <DataTable
            columns={columns}
            data={packages}
            theme={theme}
            defaultSortField="published"
            defaultSortAsc={false}
            progressPending={pending}
            progressComponent={
              <div className={classes.root}>
                <LinearProgress />
              </div>
            }
            noHeader
            pagination
            noDataComponent={
              <div style={{ padding: 24 }}>
                There are no records to display.
              </div>
            }
          />
        </CardContent>
      </Card>
    </div>
  );
}
