import React from "react";
import { Typography } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import { className } from "../function";
import Var from "../variable";
import macBookPro from '../macBookPro.svg'
import "../styles/main.css";

export default function About({ darkState }) {
  const useStyles = makeStyles((theme) => ({
    title: {
      color: darkState ? "#ffffff" : "#343a40",
      textShadow: `3px 3px 2px ${darkState ? "rgba(0, 0, 0, 1)" : "rgba(150, 150, 150, 1)"}`
    }
  }));
  const classes = useStyles();
  const { name, surname, email, state, city, about } = Var;
  return (
    <>
      <h1 className={className(classes.title, "text-6xl font-bold hp")}>
        {name} <span className="text-primary">{surname}</span>
      </h1>
      <h2 className="sub-heading mb-8">
        {`${state} · ${city} · `}
        <a title="email" href={`mailto:${email}`}>
          {email}
        </a>
      </h2>
      <img align="left" src={macBookPro} width="312px" heigth="188px" alt="macBookPro" />
      <Typography paragraph>
        {about.map((el, i) => (
          <React.Fragment key={i}>
            {el}
            {i < about.length - 1 && <br />}
          </React.Fragment>
        ))}
      </Typography>
    </>
  );
}
